        english: true
      }
    },
    computed: {
      content() {
        return this.english == true ? {home: 'Home'} : {home: 'Startseite'}
      }
    },
    created() {
        window.onscroll = () => {
          if(window.pageYOffset > 50) {
            document.querySelector('#toTopBtn').style.opacity = '1'
            document.querySelector('#toTopBtn').style.cursor = 'pointer'
          } else {
            document.querySelector('#toTopBtn').style.opacity = '0'
            document.querySelector('#toTopBtn').style.cursor = 'default'
          }
        }
    },
    methods: {
        callTrigger() {
          window.location.href='tel:555-555-5555'
